header {
  box-shadow: 0 2px 5px #dbdbdb;
  display: flex;
  justify-content: space-between;
  padding: 12px 15px;
}
header > img {
  width: 90px;
}
header > div {
  cursor: pointer;
  color: var(--global-primary-color);
}

.wrapper_right_header {
  display: flex;
  flex-direction: row;
}
.wrapper_right_header div:first-child {
  padding-right: 10px;
  color: black;
}

